import * as React from "react";
import PropTypes from "prop-types";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Box from "@mui/material/Box";

function CustomTabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
    </div>
  );
}

CustomTabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

export default function HorizontalTab({flippedCards, setFlippedCards, value, setValue}) {
  const handleChange = (event, newValue) => {
    setValue(newValue);
    if (!flippedCards.includes(newValue)) setFlippedCards([...flippedCards, newValue]);
  };

  return (
    <Box sx={{ width: "100%" }}>
      <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
        <Tabs
          value={value}
          onChange={handleChange}
          aria-label="basic tabs example"
        >
          <Tab label="Epidermis" {...a11yProps(0)} />
          <Tab label="Dermis" {...a11yProps(1)} />
          <Tab label="Hypodermis" {...a11yProps(2)} />
        </Tabs>
      </Box>
      <CustomTabPanel value={value} index={0}>
        <div style={{ top: "-30px", position: "relative", fontFamily: "Roboto, sans-serif" }}>
          <h4 style={{textDecoration: 'underline'}}>The Epidermis</h4>
          <ul>
            <li>
              It is the outermost layer of the skin. It is composed of several
              layers and various cell types crucial for its function.
            </li>
            <li>
              From the deepest to the most superficial, the epidermal layers are
              -
              <b>
                stratum basale, stratum spinosum, stratum granulosum, stratum
                lucidum, and stratum corneum
              </b>
            </li>
          </ul>
          <h4 style={{color: 'red'}}>The epidermal cells include:</h4>
          <ul>
            <li>Keratinocytes</li>
            <li>Melanocytes</li>
            <li>Langerhans cells</li>
            <li>Merkel cells (2)</li>
          </ul>
        </div>
      </CustomTabPanel>
      <CustomTabPanel value={value} index={1}>
        <div style={{ top: "-30px", position: "relative", fontFamily: "Roboto, sans-serif"  }}>
          <h4 style={{textDecoration: 'underline'}}>The Dermis</h4>
          <ul>
            <li>
              It is the middle layer of the skin, connected to the epidermis by
              the basement membrane.
            </li>
            <li>
              It consists of 2 connective tissue layers  papillary and
              reticular (1).
            </li>
            <li>
              <b>Papillary Layer:</b> it is the thinner, upper, dermal layer
              made up of loose connective tissue contacting the epidermis.
            </li>
            <li>
              <b>Reticular Layer:</b> it is the deeper layer which is thicker
              and less cellular. It consists of dense connective tissue composed
              of collagen fibre bundles.
            </li>
          </ul>

          <h4>The dermis houses:</h4>
          <ul>
            <b>
              <li>Lymph vessels</li>
              <li>Hair follicles</li>
              <li>Sweat glands</li>
              <li>Collagen bundles</li>
              <li>Fibroblasts</li>
              <li>Nerves (pain, touch, and temperature receptors)</li>
              <li>Sebaceous glands</li>
            </b>
          </ul>
          <p>
            The dermis is held together by collagen protein, giving the skin
            flexibility and strength (2)
          </p>
        </div>
      </CustomTabPanel>
      <CustomTabPanel value={value} index={2}>
        <div style={{ top: "-30px", position: "relative", fontFamily: "Roboto, sans-serif"  }}>
          <h4 style={{textDecoration: 'underline'}}>The Hypodermis</h4>
          <ul>
            <li>
              Also known as the subcutaneous fascia, the hypodermis is located
              beneath the dermis. It is the deepest skin layer.
            </li>
            <li>
              It contains adipose lobules, sensory neurons, blood vessels, and
              scanty skin appendages, such as hair follicles (1)
            </li>
          </ul>
        </div>
      </CustomTabPanel>
    </Box>
  );
}
