import React, { useState } from 'react';
import { Card, CardContent, Avatar, Typography } from '@mui/material';
import Grid from "@mui/material/Grid2";

const CardFlipComponent = ({flippedCards, setFlippedCards}) => {
  const cardData = [
    {
      img: require("./Assets/pic1.jpg"),
      title: "Barrier function",
      description: "The skin protects the body from external blocking germsthreats by regulating water, shielding against injuries, and guarding against UV damage."
    },
    {
      img: require("./Assets/pic2.jpg"),
      title: "Regulation of homeostasis",
      description: "The skin regulates temperature, water balance, and heat through blood vessels and sweat glands, controlling water absorption and evaporation."
    },
    {
      img: require("./Assets/pic3.jpg"),
      title: "Immunological defence",
      description: "The skin is vital for adaptive and innate immunity, protecting against infections, even from minor cuts."
    }
  ];

  const [flippedIndex, setFlippedIndex] = useState(null);

  const handleCardClick = (index) => {
    if (!flippedCards.includes(index)) setFlippedCards([...flippedCards, index]);
    setFlippedIndex(flippedIndex === index ? null : index);
  };

  return (
    <Grid container spacing={2} style={{ marginTop: '60px' }} alignContent="space-around">
      {cardData.map((card, index) => (
        <Grid key={index} size={{xs:12, sm:6, md:4, lg:4, xl:4}} display="flex" justifyContent="center">
          <div className="card-container" onClick={() => handleCardClick(index)}>
            <div className={`card ${flippedIndex === index ? 'flipped' : ''}`}>
              <div className="card-front">
                <Card sx={{ maxWidth: 300 }}>
                  <Grid container>
                    <Grid size={12} display="flex" justifyContent="center" alignItems="center">
                      <Avatar alt="Image" src={card.img} sx={{ width: 200, height: 200 }} />
                    </Grid>
                    <Grid size={12} display="flex" justifyContent="center" alignItems="center">
                      <CardContent>
                        <Typography gutterBottom variant="h6" component="div" style={{ color: '#6f2495', fontWeight: 'bold' }}>
                          {card.title}
                        </Typography>
                      </CardContent>
                    </Grid>
                  </Grid>
                </Card>
              </div>
              <div className="card-back">
                <Card sx={{ maxWidth: 300 }}>
                  <Grid container>
                    <Grid size={12} display="flex" justifyContent="center" alignItems="center">
                      <CardContent>
                        <Typography gutterBottom variant="h6" component="div" style={{ textAlign:'center', height: '232px', display: 'flex', justifyContent:"center", alignItems:"center" }}>
                          {card.description}
                        </Typography>
                      </CardContent>
                    </Grid>
                  </Grid>
                </Card>
              </div>
            </div>
          </div>
        </Grid>
      ))}
    </Grid>
  );
};

export default CardFlipComponent;
