import React, { useState } from "react";
import Typography from "@mui/material/Typography";
import Grid from "@mui/material/Grid2";
import { Container, Box } from "@mui/material";
import Stack from "@mui/material/Stack";
import LinearProgress from "@mui/material/LinearProgress";
import SendIcon from "@mui/icons-material/Send";
import Button from "@mui/material/Button";
import CardFlipComponent from "./CardFlipComponent";

const Chapter1 = ({ setChapter }) => {
  const [flippedCards, setFlippedCards] = useState([]);
  return (
    <>
      <Container style={{ marginTop: "20px", marginBottom: '80px' }}>
        <Stack spacing={2}>
          <Typography
            variant="h3"
            gutterBottom
            className="chapter-heading"
          >
            Primary Functions of the Skin
          </Typography>
          <Box
            component="section"
            sx={{ p: 1, border: "1px solid #aa0061" }}
            style={{ textAlign: "center", color: "#aa0061" }}
          >
            The skin has comprehensive roles that are important for maintaining
            overall health and well-being. We can group the primary roles of the
            skin within the following categories. Lets discuss these categories
            one by one.
          </Box>
          <CardFlipComponent
            flippedCards={flippedCards}
            setFlippedCards={setFlippedCards}
          />
        </Stack>
      </Container>

      <div
        style={{
          width: "100%",
          bottom: "0px",
          left: "0px",
          height: "80px",
          position: "fixed",
          background: 'white',
        }}
      >
        <Box sx={{ display: "flex", alignItems: "center" }} style={{ paddingRight: "10px", paddingLeft: "10px" }}>
          <Box sx={{ width: "100%", mr: 1 }}>
            <LinearProgress
              variant="determinate"
              value={16.7 * flippedCards.length}
            />
          </Box>
          <Box sx={{ minWidth: 35 }}>
            <Typography variant="body2" sx={{ color: "text.secondary" }}>
              {`${Math.round(16.7 * flippedCards.length)}% `}
            </Typography>
          </Box>
        </Box>
        <Grid container>
          <Grid
            size={{xs:6, sm:3, md:3, lg:3, xl:3}}
            className="footer-image"
          >
            <img src={require("./Assets/footer.png")} alt={"companyLogo"} style={{width: "150px"}}/>
          </Grid>
          <Grid
            size={{xs:0, sm:6, md:6, lg:6, xl:6}}
            className="footer-text"
          >
             2024 Convatec | Confidential and Proprietary
          </Grid>
          <Grid
            size={{xs:6, sm:3, md:3, lg:3, xl:3}}
            display="flex"
            justifyContent="center"
            alignItems="center"
          >
            <Button
              variant="contained"
              endIcon={<SendIcon />}
              disabled={16.7 * flippedCards.length < 50}
              onClick={() => setChapter(2)}
            >
              Next
            </Button>
          </Grid>
        </Grid>
      </div>
    </>
  );
};

export default Chapter1;
