import React, { useState } from "react";
import Typography from "@mui/material/Typography";
import Grid from "@mui/material/Grid2";
import { Box, Container } from "@mui/material";
import Stack from "@mui/material/Stack";
import LinearProgress from "@mui/material/LinearProgress";
import CheckIcon from "@mui/icons-material/Check";
import Button from "@mui/material/Button";
import HorizontalTab from "./HorizontalTab";

const Chapter2 = ({ setChapter }) => {
  const [flippedCards, setFlippedCards] = useState([0]);
  const [value, setValue] = React.useState(0);

  return (
    <>
      <Container style={{ marginTop: "20px" }}>
        <Stack spacing={2}>
          <Typography
            variant="h3"
            gutterBottom
            className="chapter-heading"
          >
            Layers of the skin
          </Typography>
          <Box
            component="section"
            sx={{ p: 1, border: "1px solid #aa0061" }}
            style={{ textAlign: "center", color: "#aa0061" }}
          >
            The skin has 3 distinct layers, each having different anatomical
            structures and functions . Together, they enable the skin to perform
            its basic primary functions. View and navigate through the the
            diagram below to learn more about the layers of the skin.
          </Box>
        </Stack>
      </Container>
      <Grid
        container
        spacing={2}
        display="flex"
        alignContent="space-between"
        style={{
          marginTop: "25px",
          marginBottom: "50px",
          position: "relative",
          width: "100%",
        }}
      >
        <Grid
          size={{ xs: 12, sm: 12, md: 5 }}
          display="flex"
          justifyContent="flex-start"
          alignItems="flex-start"
        >
          <img
            src={require("./Assets/pic4.png")}
            alt="skin layers"
            style={{ position: "relative", width: "-webkit-fill-available" }}
          />
          <div
            name="option1"
            className="options option1"
            onClick={() => {
              if (!flippedCards.includes(0))
                setFlippedCards([...flippedCards, 0]);
              setValue(0);
            }}
          />
          <div
            name="option2"
            className="options option2"
            onClick={() => {
              if (!flippedCards.includes(1))
                setFlippedCards([...flippedCards, 1]);
              setValue(1);
            }}
          />
          <div
            name="option3"
            className="options option3"
            onClick={() => {
              if (!flippedCards.includes(2))
                setFlippedCards([...flippedCards, 2]);
              setValue(2);
            }}
          />
        </Grid>
        <Grid size={{ xs: 12, sm: 12, md: 7 }} className="info-tab">
          <HorizontalTab
            flippedCards={flippedCards}
            setFlippedCards={setFlippedCards}
            value={value}
            setValue={setValue}
          />
        </Grid>
      </Grid>
      <div
        style={{
          width: "100%",
          bottom: "0px",
          left: "0px",
          height: "80px",
          position: "fixed",
          background: "white",
          zIndex: "2",
        }}
      >
        <Box sx={{ display: "flex", alignItems: "center" }} style={{ paddingRight: "10px", paddingLeft: "10px" }}>
          <Box sx={{ width: "100%", mr: 1 }}>
            <LinearProgress
              variant="determinate"
              value={
                flippedCards.length === 0 ? 50 : 16.7 * flippedCards.length + 50
              }
            />
          </Box>
          <Box sx={{ minWidth: 35 }}>
            <Typography variant="body2" sx={{ color: "text.secondary" }}>
              {`${Math.round(
                flippedCards.length === 0 ? 50 : 16.7 * flippedCards.length + 50
              )}% `}
            </Typography>
          </Box>
        </Box>

        <Grid container style={{ paddingRight: "10px", paddingLeft: "10px" }}>
          <Grid
            size={{xs:6, sm:3, md:3, lg:3, xl:3}}
            className="footer-image"
          >
            <img src={require("./Assets/footer.png")} alt={"companyLogo"} style={{width: "150px"}}/>
          </Grid>
          <Grid
            size={{xs:0, sm:6, md:6, lg:6, xl:6}}
            className="footer-text"
          >
             2024 Convatec | Confidential and Proprietary
          </Grid>
          <Grid
            size={{xs:6, sm:3, md:3, lg:3, xl:3}}
            display="flex"
            justifyContent="center"
            alignItems="center"
          >
            <Button
              variant="contained"
              endIcon={<CheckIcon />}
              disabled={16.7 * flippedCards.length < 50}
              onClick={() => setChapter(3)}
              color="success"
            >
              Completed
            </Button>
          </Grid>
        </Grid>
      </div>
    </>
  );
};

export default Chapter2;
